.pagination {
	padding: 10px 0 0;
	display: block;
	margin-bottom: 40px;

	ul {
		padding: 0;
		margin: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		list-style-type: none; }

	li {
		display: inline-flex;
		height: 45px;
		margin: 0 4px;
		text-align: center;
		position: relative;
		@include r(575) {
			height: 40px;
			margin: 0 1px; }
		@include r(374) {
			height: 30px; } }

	a,
	span {
		display: block;
		width: 45px;
		height: 100%;
		color: #375A9E;
		font-size: 17px;
		line-height: 45px;
		border-radius: 50%;
		text-decoration: none;
		position: relative;
		@include r(575) {
			width: 40px;
			line-height: 40px;
			border-radius: 4px; }
		@include r(374) {
			width: 30px;
			line-height: 30px; } }

	a {

		&.prev,
		&.next {
			border: 1px solid #00FF24;

			span {
				display: none; }

			&::before {
				position: absolute;
				content: '';
				width: 10px;
				height: 10px;
				left: 50%;
				top: 50%;
				border: solid #fff;
				box-sizing: border-box;
				transform: translate(-50%, -50%) rotate(45deg); }

			&:hover {

				&::before {
					border-color: #375A9E; } } }

		&.prev {

			&::before {
				border-width: 0 0 2px 2px; } }

		&.next {

			&::before {
				border-width: 2px 2px 0 0; } }

		&:hover {
			color: $color-white; } }

	.current {
		color: $color-white;
		text-decoration: none; } }
