@import "../helpers/mixins";
@import "../base/variables";

.header {

	&__wrapper {
		background-color: #162251;
		padding: 25px 0;
		@include r(991) {
			padding: 15px 0; } }

	&__inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		@include r(991) {
			justify-content: space-between; } }

	&__logo {
		@include r(991) {
			flex-grow: 1; }

		a {
			@include r(575) {
				display: inline-block;
				max-width: 160px; }
			@include r(374) {
				max-width: 120px; } } }

	&__btns {
		display: flex; }

	&__favorites {

		&-btn {
			width: 26px;
			height: 26px;
			@include svg(favorites);
			background-repeat: no-repeat;
			background-position: center; } }

	&__search {
		padding-top: 10px;
		width: 100%;
		display: none;
		order: 4;

		&-btn {
			width: 26px;
			height: 26px;
			position: relative;
			background-image: linear-gradient(90deg, #FF2053 0%, #FF3F00 100%);
			border-radius: 50%;
			margin-left: 15px;
			cursor: pointer;
			order: 2;
			@include r(374) {
				margin-left: 20px;
				width: 50px;
				height: 50px;
				border-radius: 4px; }

			&::before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				@include svg(search);
				background-repeat: no-repeat;
				background-position: center; }

			&.is-open {

				&::before {
					@include svg(close); } } }

		&.is-open {
			display: block; }

		.cf {
			display: flex;

			input {
				height: 50px;
				border: 0;
				outline: none;
				background-color: #375a9e;

				&[type=search] {
					padding: 0 38px;
					border-radius: $border-radius 0 0 $border-radius;
					flex-grow: 1;
					@include placeholder-color(#162251);
					@include r(991) {
						padding: 0 20px; } }

				&[type=submit] {
					width: 60px;
					@include svg(search);
					background-repeat: no-repeat;
					background-position: center;
					background-size: 20px;
					border-radius: 0 $border-radius $border-radius 0;
					@include r(575) {
						width: 40px; } } } } } }
