.casino {
	width: 100%;
	padding-left: 15px;
	padding-right: 15px;

	&:not(:last-child) {
		margin-bottom: 10px; }

	&__items {
		counter-reset: cas;
		margin-bottom: 100px; }

	&__inner {
		padding: 20px;
		min-height: 133px;
		display: flex;
		align-items: center;
		background-color: #375A9E;
		border-radius: 20px;
		@include r(1259) {
			flex-wrap: wrap;
			align-items: center;
			justify-content: center; } }

	&__logo {
		padding-left: 40px;
		display: flex;
		align-items: center;
		position: relative;
		margin-right: 30px;
		@include r(1259) {
			margin-bottom: 20px; }

		&::after,
		&::before {
			position: absolute;
			width: 20px;
			top: 50%;
			left: 0;
			font-weight: 500;
			letter-spacing: 0.05em;
			text-align: center;
			background: linear-gradient(to bottom, #ffffff 0%, #ffffff 25%, #375A9E 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#375A9E',GradientType=1 );
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent; }

		&::after {
			content: '#';
			font-size: 22px;
			line-height: 26px;
			transform: translateY(-34px); }

		&::before {
			counter-increment: cas;
			content: counter(cas);
			font-size: 35px;
			line-height: 41px;
			transform: translateY(-8px); } }

	&__rating {
		text-align: center;
		margin-right: 20px;
		@include r(1259) {
			margin-bottom: 20px; }

		&-average {
			font-size: 25px;
			font-weight: 700;
			line-height: 29px;
			margin-bottom: 5px; } }

	&__bonus {
		max-width: 171px;
		text-align: center;
		margin-right: 20px;
		@include r(1259) {
			margin-bottom: 20px; }

		span {
			padding: 6px 20px;
			min-width: 80px;
			display: inline-block;
			background: rgba(#132A57, .4);
			border-radius: 20px;
			text-align: center;
			margin-right: 6px; } }

	&__features {
		padding: 0;
		margin: 0;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		list-style-type: none;
		@include r(767) {
			flex-grow: 0;
			margin-bottom: 20px; }

		li {
			position: relative;
			font-size: 16px;
			line-height: 22px;
			padding-left: 24px;

			&::before {
				position: absolute;
				content: '';
				width: 14px;
				height: 14px;
				top: 3px;
				left: 0;
				z-index: 1;
				@include svg(check); }

			&:not(:last-child) {
				margin-bottom: 5px; } } }

	&__nav {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		@include r(1259) {
			flex-direction: row;
			flex-grow: 1; }

		.btn {

			+ .btn {
				margin-top: 5px;
				@include r(1259) {
					margin-top: 0;
					margin-left: 15px; } }

			&-review,
			&-site {
				width: 123px;
				@include r(374) {
					width: 110px; } } } } }
