}

blockquote {
  padding: 15px 40px;
  margin: 0 0 25px;
  position: relative;
  color: #272D2F;
  font-size: 17px;
  font-style: italic;
  font-weight: 400;
  line-height: 27px;
}

@media only screen and (max-width: 767px) {
  blockquote {
    padding: 15px;
    margin-left: 15px;
  }
}

blockquote::before, blockquote::after {
  position: absolute;
  content: '';
  width: 12px;
  height: 24px;
  top: 0;
  border: 3px solid #3754dc;
  border-radius: 0 6px 6px 6px;
}

blockquote::before {
  left: 0;
}

blockquote::after {
  left: 16px;
}

blockquote *:first-child {
  margin-top: 0;
}

blockquote *:last-child {
  margin-bottom: 0;
}

strong {
  font-weight: 700;
}

.bold {
  font-weight: 700;
}

.container {
