    padding: 20px 15px;
  }
}

.the_content a {
  color: #FFCE22;
  text-decoration: none;
}

.the_content a:hover, .the_content a:focus, .the_content a:active {
  color: #bb9200;
}

.the_content img {
  height: auto;
}

.the_content ul,
.the_content ol {
  margin: 0 0 25px;
  padding: 0 0 0 30px;
  list-style-type: none;
}

@media only screen and (max-width: 575px) {
  .the_content ul,
  .the_content ol {
    padding: 0;
  }
}

.the_content ul li,
.the_content ol li {
  padding-left: 40px;
  margin-bottom: 10px;
  position: relative;
}

.the_content ul li::before {
  content: ' ';
  position: absolute;
  width: 22px;
  height: 22px;
  top: 0;
  left: 0;
  background-image: url("../img/svg/ul.svg");
}

.the_content ol {
  counter-reset: list;
}

.the_content ol li::before {
  content: ' ';
  position: absolute;
  counter-increment: list;
  content: counter(list) ".";
  width: 40px;
  height: 37px;
  top: 1px;
  left: -10px;
  color: #00FF24;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
}

.the_content.page_descr {
  color: #fff;
  margin-bottom: 40px;
}

.page_title {
  font-size: 36px;
  line-height: 50px;
  margin: 0 0 9px;
}

@media only screen and (max-width: 991px) {
  .page_title {
    font-size: 28px;
    line-height: 45px;
  }
}

@media only screen and (max-width: 767px) {
  .page_title {
    font-size: 24px;
    line-height: 35px;
  }
}

.percent-bar {
  display: block;
  position: relative;
  width: 150px;
  height: 150px;
}

.percent-bar svg {
  position: relative;
  width: 150px;
  height: 150px;
}

.percent-bar svg circle {
  width: 100%;
  height: 100%;
  fill: none;
  transform: rotate(-90deg) translate(4px, 4px);
  transform-origin: 50% 50%;
  stroke: #2e47d7;
  stroke-width: 8px;
  stroke-linecap: round;
  stroke-dasharray: 446;
  stroke-dashoffset: 446;
}

.percent-bar .thumb {
  position: absolute;
  top: 15px;
  left: 15px;
  border-radius: 50%;
}

.percent-bar .number {
  position: absolute;
  bottom: 0;
  right: -20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 18px;
  color: rgba(0, 208, 255, 0.5);
}

.section {
  margin-bottom: 100px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
