}

.no-posts {
  padding-left: 15px;
  padding-right: 15px;
}

.breadcrumbs {
  font-size: 17px;
  line-height: 24px;
  color: #fff;
  margin-bottom: 3px;
}

@media only screen and (max-width: 767px) {
  .breadcrumbs {
    font-size: 15px;
    line-height: 22px;
  }
}

@media only screen and (max-width: 575px) {
