}

.pagination {
  padding: 10px 0 0;
  display: block;
  margin-bottom: 40px;
}

.pagination ul {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.pagination li {
  display: inline-flex;
  height: 45px;
  margin: 0 4px;
  text-align: center;
  position: relative;
}

@media only screen and (max-width: 575px) {
  .pagination li {
    height: 40px;
    margin: 0 1px;
  }
}

@media only screen and (max-width: 374px) {
  .pagination li {
    height: 30px;
  }
}

.pagination a,
.pagination span {
  display: block;
  width: 45px;
  height: 100%;
  color: #375A9E;
  font-size: 17px;
  line-height: 45px;
  border-radius: 50%;
  text-decoration: none;
  position: relative;
}

@media only screen and (max-width: 575px) {
