  .burger {
    margin-left: 15px;
    width: 50px;
    height: 50px;
    border-radius: 4px;
  }
}

.burger::before, .burger::after {
  position: absolute;
  content: '';
  height: 3px;
  left: calc(50% - 16px);
  top: calc(50% - 1px);
  background-color: #fff;
  transition: all .2s ease;
}

.burger::before {
  width: 32px;
  margin-top: -4px;
}

.burger::after {
  width: 24px;
  margin-top: 4px;
}

.burger.is-open::before, .burger.is-open::after {
  margin-top: 0;
}

.burger.is-open::before {
  transform: translateY(-50%) rotate(45deg);
}

.burger.is-open::after {
  width: 32px;
  transform: translateY(-50%) rotate(-45deg);
}

