@import "../helpers/mixins";
@import "../base/variables";

.container {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@include rmin(1260) {
		max-width: 1200px; }
	@include rmin(992) {
		max-width: 960px; }
	@include rmin(768) {
		max-width: 720px; }
	@include rmin(576) {
		max-width: 540px; } }

.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px; }

.item {
	width: 100%;
	padding-left: 15px;
	padding-right: 15px; }

.site__wrapper {
	background-image: url('../img/svg/bg-top-left.svg'), url('../img/svg/bg-top-right.svg'), url('../img/svg/bg-bottom-left.svg'), url('../img/svg/bg-bottom-right.svg');
	background-position: left top, right top, left bottom, right bottom;
	background-repeat: no-repeat; }

.content {

	&__wrapper {
		padding: 50px 0 0;
		margin-bottom: 100px;
		@include r(991) {
			margin-bottom: 0; }

		&.no-padding {
			padding-top: 0; } }

	&__inner {
		display: flex;
		flex-wrap: wrap;
		margin-left: -15px;
		margin-right: -15px; }

	&__main {
		padding-left: 15px;
		padding-right: 15px;
		width: 75%;
		order: 2;
		@include r(1259) {
			width: 66.66667%; }
		@include r(991) {
			width: 100%;
			margin-bottom: 50px;
			order: 1; }

		&.no-sidebar {
			width: 100%; } }

	&__sidebar {
		padding-left: 15px;
		padding-right: 15px;
		width: 25%;
		order: 1;
		@include r(1259) {
			width: 33.33333%; }
		@include r(991) {
			width: 100%;
			margin-bottom: 50px;
			order: 2; } } }

.the_content {

	&-bg {
		padding: 30px;
		color: $color-white;
		background-color: #375A9E;
		border-radius: $border-radius;
		@include r(575) {
			padding: 20px 15px; } }

	a {
		color: #FFCE22;
		text-decoration: none;

		&:hover,
		&:focus,
		&:active {
			color: darken(#FFCE22, 20%); } }

	img {
		height: auto; }

	ul,
	ol {
		margin: 0 0 25px;
		padding: 0 0 0 30px;
		list-style-type: none;
		@include r(575) {
			padding: 0; }

		li {
			padding-left: 40px;
			margin-bottom: 10px;
			position: relative; } }

	ul {

		li {
			@include pseudo(before) {
				width: 22px;
				height: 22px;
				top: 0;
				left: 0;
				@include svg(ul); } } }

	ol {
		counter-reset: list;

		li {
			@include pseudo(before) {
				counter-increment: list;
				content: counter(list)'.';
				width: 40px;
				height: 37px;
				top: 1px;
				left: -10px;
				color: #00FF24;
				font-size: 16px;
				line-height: 22px;
				text-align: center; } } }

	&.page_descr {
		//font-size: 22px
		color: $color-white;
		margin-bottom: 40px;
		//+r(991)
		//	font-size: 19px
		//+r(767)
 } }		//	font-size: 16px

.page_title {
	font-size: 36px;
	line-height: 50px;
	margin: 0 0 9px;
	@include r(991) {
		font-size: 28px;
		line-height: 45px; }
	@include r(767) {
		font-size: 24px;
		line-height: 35px; } }

.percent-bar {
	display: block;
	position: relative;
	width: 150px;
	height: 150px;

	svg {
		position: relative;
		width: 150px;
		height: 150px;

		circle {
			width: 100%;
			height: 100%;
			fill: none;
			transform: rotate(-90deg) translate(4px,4px);
			transform-origin: 50% 50%;
			stroke: $color-blue;
			stroke-width: 8px;
			stroke-linecap: round;
			stroke-dasharray: 446;
			stroke-dashoffset: 446; } }

	.thumb {
		position: absolute;
		top: 15px;
		left: 15px;
		border-radius: 50%; }

	.number {
		position: absolute;
		bottom: 0;
		right: -20px;
		font-weight: 700;
		font-size: 20px;
		line-height: 18px;
		color: rgba( $color-blue-medium, .5 ); } }

.section {
	margin-bottom: 100px; }

.section-header {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	margin-bottom: 25px;
	@include r(767) {
		flex-direction: column; }

	h2 {
		padding: 4px 0;
		margin: 0;
		@include r(767) {
			margin: 0 0 15px; } } }

.screens-box {
	margin: 0 -30px 30px;
	padding: 30px;
	background-color: $color-text;

	.swiper-slide {
		text-align: center; }

	.swiper-pagination-bullets {
		padding-top: 15px;
		display: flex;
		justify-content: center; }

	.swiper-pagination-bullet {
		width: 10px;
		height: 10px;
		background-color: $color-white;
		opacity: 1;
		border-radius: $border-radius;
		transition: $transition;

		&.swiper-pagination-bullet-active {
			width: 30px;
			height: 10px;
			background-color: $color-green; } } }


#toc_container {
	background-color: $color-blue-light;
	border: 0;
	border-radius: 5px;
	width: 100%;
	padding: 20px;

	li {

		&::before {
			display: none; } }

	a {
		display: inline-block;
		margin-bottom: 5px;
		text-decoration: none;
		color: $color-text; }

	.toc_number {
		color: $color-blue;
		font-weight: 700; }

	p {

		&.toc_title {
			font-size: 20px;
			text-align: left; } } }

.no-posts {
	padding-left: 15px;
	padding-right: 15px; }
